import React from 'react';
import {connect} from 'react-redux'; 
import ExpenseForm from './ExpenseForm';
import { editExpense, removeExpense } from '../actions/expenses';

const EditExpensePage = (props) => {
    return(
        <div>
            <ExpenseForm
                expense = {props.expense}
                onSubmit = {(expense) => {                    
                    props.dispatch(editExpense(props.expense.id,expense));
                    props.history.push('/');
                }}
        />
        <button onClick = {(e)=> {
            //here object of remove expense we want to set so we have to keep id: something
            props.dispatch(removeExpense({id : props.expense.id}))
            props.history.push('/');
        }}>Remove</button>
        </div>
    );
    
};

const mapStateToProps = (state,props) => {    
    return {
        expense: state.expenses.find((expense) => {
            return expense.id === props.match.params.id
            // or expense : state.expenses.find((expense) => expense.id === props.match.params.id)
        })
    }
}

export default connect(mapStateToProps)(EditExpensePage);

//we use connect to connect it to store such that we cn have the expense value and not only the id of particular clicked item 