export const setTextFilter = (text) => ({
    type: 'SET_TEXT_FILTER',
    text
})

export const sortByDate = () => ({
    type: 'SORT_BY_DATE'
})

export const sortByAmount = () => ({
    type: 'SORT_BY_AMOUNT'
})

export const setStartDate = (date) => ({
    type: 'SET_START_DATE',
    date
})
//we dont want to keep value when not send to undefined as it occurs independently
export const setEndDate = (date) => ({
    type: 'SET_END_DATE',
    date
})